/* eslint-disable no-undef */
import { BACKEND_BASE_URL as backendBaseUrl } from 'process.env';

const wdlHttpClient = {
  get: (path, baseUrl = null) => {
    return new Promise((r, e) =>
      $.ajax({
        method: 'GET',
        url: `${baseUrl || backendBaseUrl}${path}`,
        headers: {
          'X-App-ID': window.WDL_SITE_ID,
        },
        success: function (data) {
          r(data);
        },
        error: function (error) {
          e(error);
        },
      })
    );
  },
  post: (path, data = {}, baseUrl = null) => {
    return new Promise((r, e) =>
      $.ajax({
        method: 'POST',
        url: `${baseUrl || backendBaseUrl}${path}`,
        data,
        headers: {
          'X-App-ID': window.WDL_SITE_ID,
        },
        success: function (data) {
          r(data);
        },
        error: function (error) {
          e(error);
        },
      })
    );
  },
};

export { wdlHttpClient };
