/* eslint-disable no-undef */
const displayUser = ({ user, blackListedPages = [] }) => {
  const currentPath = window.location.pathname;

  if (!blackListedPages.includes(currentPath)) {
    $('[data-wdl-user]').each(function () {
      const field = $(this).attr('data-wdl-user');

      if (field && user[field]) {
        $(this).val(user[field] || '');
      }
    });
  }
};

export default displayUser;
