/* eslint-disable no-undef */
import bootstrap from './bootstrap';
import LoaderView from './views/loader';
import Settings from './settings';

const WdlCore = (async () => {
  const ctx = await bootstrap();

  return {
    getSettings: Settings.getSettings,
    context: ctx,
    views: {
      loader: LoaderView,
    },
  };
})();

export default WdlCore;
