const loaderView = document.createElement('div');
loaderView.classList.add(['relative', 'z-10']);
loaderView.role = 'dialog';
loaderView.ariaModal = true;

loaderView.innerHTML = `<div style='position: absolute; height: 100vh; width: 100%; background-color: #fff; top: 0' id="wdl-loader">
  <div style='display: flex; flex-direction: column; padding: 10px; justify-content: center; align-items: center; width: 100%; height: 100vh'>
    <svg
      version='1.1'
      xmlns='http://www.w3.org/2000/svg'
      xmlns:xlink='http://www.w3.org/1999/xlink'
      x='0px'
      y='0px'
      viewBox='0 0 100 100'
      enable-background='new 0 0 0 0'
      xml:space='preserve'
      style='width: 6rem'
    >
      <circle fill='#000' stroke='none' cx='6' cy='50' r='6'>
        <animate attributeName='opacity' dur='1s' values='0;1;0' repeatCount='indefinite' begin='0.1' />
      </circle>
      <circle fill='#000' stroke='none' cx='26' cy='50' r='6'>
        <animate attributeName='opacity' dur='1s' values='0;1;0' repeatCount='indefinite' begin='0.2' />
      </circle>
      <circle fill='#000' stroke='none' cx='46' cy='50' r='6'>
        <animate attributeName='opacity' dur='1s' values='0;1;0' repeatCount='indefinite' begin='0.3' />
      </circle>
    </svg>
  </div>
</div>`;

const show = () => {
  document.body.appendChild(loaderView);
};

const hide = () => {
  // eslint-disable-next-line no-undef
  $('#wdl-loader').remove();
};

export default { show, hide };
