/**
 * Local Cache is used for fast retrieval on local
 * It also helps switch between localStorage, cookies or any other local mechanistm for storage
 */

const LocalCache = {
  get: (key) => {
    if (!key) return;
    if (localStorage) {
      // fallback should be cookies
      return localStorage.getItem(key);
    }
  },
  set: (key, data) => {
    if (!data || !key) return;
    if (localStorage) {
      // fallback should be cookies
      return localStorage.setItem(key, data);
    }
  },
  delete: (key) => {
    if (LocalCache.get(key)) {
      localStorage.removeItem(key);
    }
  },
  nukeAll: () => {
    // TODO - implement this to delete all
  },
};

export default LocalCache;
