/* eslint-disable no-undef */
import { USER_ACCOUNT_PAGE as userAccountPage } from 'process.env';

import WdlCore from '../pkg.core';
import displayUser from './display-user';
import currentUser from './parse-current-user';
import userMgt from './user-management';

const userInfoPath = userAccountPage;

const WdlMemberships = (() => {
  // If page is user-account, put a blank on it first
  const currentPath = (window?.location?.pathname || '').trim();
  console.log('aaaaaaa', { userAccountPage, currentPath });

  // Fetch user data on user account page on webflow
  if (currentPath === userInfoPath) {
    console.log('bbbbbb');
    (async () => {
      const core = await WdlCore;
      await currentUser.persistCurrentUser({ core, currentPath });
    })();
  }

  // Used to display user using custom weedle data classes
  displayUser({ blackListedPages: [userInfoPath], user: userMgt.getCurrentUser() });

  return {
    currentUser: userMgt.getCurrentUser,
    logoutUser: userMgt.logoutUser,
  };
})();

window.WdlMemberships = WdlMemberships;
