import LocalCache from '../pkg.core/services/local-cache';
import { LocalCachekeys } from '../pkg.core/utils/constants';

const getCurrentUser = () => {
  const user = LocalCache.get(LocalCachekeys.currentUserKey);

  return user !== null ? JSON.parse(user) : null;
};

const logoutUser = () => {
  LocalCache.delete(LocalCachekeys.currentUserKey);
};

export default { getCurrentUser, logoutUser };
