import LocalCache from '../pkg.core/services/local-cache';
import { LocalCachekeys } from '../pkg.core/utils/constants';

const { get, set } = Object.getOwnPropertyDescriptor(HTMLInputElement.prototype, 'value');

function getInputOnChange(inp, callback) {
  Object.defineProperty(inp, 'value', {
    get() {
      return get.call(this);
    },
    set(newVal) {
      callback(newVal);
      // eslint-disable-next-line no-setter-return
      return set.call(this, newVal);
    },
  });
}

const persistCurrentUser = async ({ core, currentPath }) => {
  core.views.loader.show();
  const output = {};
  const config = await core.getSettings();

  // eslint-disable-next-line no-undef
  const totalFields = $('[id^="wdl-user"]').length;
  // eslint-disable-next-line no-undef
  $('[id^="wdl-user"]').each(function () {
    const obj = this;

    getInputOnChange(this, function (newVal) {
      output[obj.id] = newVal;

      // eslint-disable-next-line no-undef
      if (Object.keys(output).length === totalFields) {
        LocalCache.set(LocalCachekeys.currentUserKey, JSON.stringify(output));
        // Save user to localStorage
        if (!config?.wfMemberships?.redirectToOnLogin || config.wfMemberships.redirectToOnLogin === currentPath) {
          core.views.loader.hide();
        } else {
          window.location.replace(config.wfMemberships.redirectToOnLogin);
        }
      }
    });
  });
};

export default { persistCurrentUser };
