/* eslint-disable no-undef */

import debug from 'debug';
import { BACKEND_BASE_URL as backendBaseUrl } from 'process.env';

const log = debug('app:log');

// eslint-disable-next-line no-undef
if (ENV !== 'production') {
  // Enable the logger
  debug.enable('*');
  log('Logging is enabled!');
} else {
  debug.disable();
}

const envs = {
  backendBaseUrl,
};

/*
  Check if browser has the following and return true/false. This should be used to display an error where possible
    - localStorage
*/

// const checkBrowserCompatibility = () => {};

const ctx = { log };

const init = async () => {
  ctx.envs = envs;
  const clientId = window.WDL_SITE_ID;

  if (!clientId) {
    throw new Error('Missing weedle app installation.');
  }

  ctx.clientId = clientId;

  return ctx;
};

export default init;
